html {
  height: 100%;
}

body {
  background-color: #d9d9d9;
  height: 100%;
}

#root {
  height: 100%;
}

.container {
  height: 100%;
}

.logo {
  height: 64px;
}

.home {
  height: 100%;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
  padding: 1rem;
  background-color: #f2f2f2;
}

.telegram {
  transform: rotate(320deg);
}

@media only screen and (max-width: 601px) {
  .logo {
    height: 56px;
  }

  h3 {
    font-size: 2.3rem !important;
  }
}
